export const sourceCode = "import { Callout } from 'nextra/components'\n\n# List Icons\n\nBoxicons makes it easy to use icons as custom bullets for lists. This is useful for feature lists, checklists, menus, or any place where you want more visual clarity than standard bullet points.\n\nTo enable icon bullets, add the `bx-ul` class to your `<ul>` element and place an icon inside each `<li>` item.\n<br/>\n---\n<Callout type=\"warning\">\n Make sure you have included the transformations stylesheet before using these classes.\n</Callout>\n\n\n```html filename=\"transformations.min.css\" copy\n<link href=\"https://cdn.boxicons.com/transformations.min.css\" rel=\"stylesheet\">\n```\n\n```html copy\n<ul class='bx-ul'>\n<li><i class='bx  bx-right-arrow'></i>List Item 1</li>\n<li><i  class='bx  bx-x'></i>List Item 2</li>\n<li><i  class='bx  bx-plus'></i>List Item 3</li>\n</ul>\n```\n<div className=\"icon-display\">\n<ul className='bx-ul' style={{ fontSize: '24px' }}>\n<li style={{ alignItems: 'center', display: 'flex', gap: '10px' }}><i className='bxdocs  bxrf-200-happy'></i>List Item 1</li>\n<li style={{ alignItems: 'center', display: 'flex', gap: '10px' }}><i className='bxdocs  bxrf-200-shocked'></i>List Item 2</li>\n<li style={{ alignItems: 'center', display: 'flex', gap: '10px' }}><i className='bxdocs  bxrf-200-dizzy'></i>List Item 3</li>\n</ul>\n</div>\n\n#### How it works\n\n* The `bx-ul` class removes the default list styling and aligns icons automatically.\n* Each list item contains an icon element followed by the text.\n* Icons are positioned and spaced consistently so all items line up neatly.\n\nThis ensures proper alignment even when list items wrap onto multiple lines.";
