export const sourceCode = "# Sizing\n\nBoxicons scale just like text. You can control the size of an icon using the CSS `font-size` property, and by default, icons inherit the `font-size` of their parent element.\n\nIn addition to using CSS, Boxicons also provides a set of utility classes for common, consistent icon sizes.\n<br/>\n---\n\n### Using CSS Font Size\n\nYou can resize icons using plain CSS:\n\n```css\ni.bx {\n  font-size: 32px;\n}\n```\nOr inline styles:\n```html copy\n<i class=\"bxf bx-thumb-up\" style=\"font-size: 96px;\"></i>\n```\n<div className=\"icon-display\">\n<i className=\"bxdocs bxrf-200-thumb-up\" style={{ fontSize: '96px' }}></i>\n\n</div>\n\n### Import the transformations CSS\nTo use the following classes, you first need to include the transformations stylesheet.\n\n```html filename=\"transformations.min.css\" copy\n<link href=\"https://cdn.boxicons.com/transformations.min.css\" rel=\"stylesheet\">\n```\n\n### Using Size Utility Classes\nBoxicons provides predefined size classes:\n* `bx-xs`\n* `bx-sm`\n* `bx-md`\n* `bx-lg`\n* `bx-xl`\n* `bx-2xl`\n* `bx-3xl`\n* `bx-4xl`\n* `bx-5xl`\n\n```html copy\n<i class=\"bx bx-thumb-up bx-xs\"></i>\n<i class=\"bx bx-thumb-up bx-sm\"></i>\n<i class=\"bx bx-thumb-up bx-md\"></i>\n<i class=\"bx bx-thumb-up bx-lg\"></i>\n<i class=\"bx bx-thumb-up bx-xl\"></i>\n<i class=\"bx bx-thumb-up bx-2xl\"></i>\n<i class=\"bx bx-thumb-up bx-3xl\"></i>\n<i class=\"bx bx-thumb-up bx-4xl\"></i>\n<i class=\"bx bx-thumb-up bx-5xl\"></i>\n```\n<div className=\"icon-display\">\n<i className=\"bxdocs bxrf-200-thumb-up bx-xs\"></i>\n<i className=\"bxdocs bxrf-200-thumb-up bx-sm\"></i>\n<i className=\"bxdocs bxrf-200-thumb-up bx-md\"></i>\n<i className=\"bxdocs bxrf-200-thumb-up bx-lg\"></i>\n<i className=\"bxdocs bxrf-200-thumb-up bx-xl\"></i>\n<i className=\"bxdocs bxrf-200-thumb-up bx-2xl\"></i>\n<i className=\"bxdocs bxrf-200-thumb-up bx-3xl\"></i>\n<i className=\"bxdocs bxrf-200-thumb-up bx-4xl\"></i>\n<i className=\"bxdocs bxrf-200-thumb-up bx-5xl\"></i>\n</div>\nThese classes apply consistent, predefined sizes that work well across common UI layouts.\n\n### Fixed Width Icons\n\nSometimes you need icons to align neatly in lists or menus. For this, you can use the bx-fw (fixed width) utility class, which gives all icons the same width.\nThis is especially useful when placing icons next to text, such as in navigation lists or menus.\n\n```html copy\n<i class=\"bx-fw bxl bx-facebook-square\"></i> Facebook\n```\n<div className=\"icon-display\">\n<i className=\"bxdocs bx-facebook-circle bx-fw\"></i> <span style={{ fontSize: '25px',fontWeight: 600 }}>Facebook</span>\n\n</div>";
