export const sourceCode = "# Colors\n\nBoxicons can be styled using standard CSS, making it easy to change icon colors to match your design system. You can apply colors using the `style` attribute, CSS classes, or inherited styles from parent elements.\n<br/>\n---\n<br/>\n## Basic & Brand Icons\n\nFor Basic and Brand icons, you can set the color using the CSS `color` property. By default, icons inherit the text color of their parent element.\n\nExample:\n\n```html copy\n<i class=\"bx bx-heart\" style=\"color:#fb2d2d\"></i>\n<i class=\"bxl bx-twitter\" style=\"color:#1da1f2\"></i>\n```\n<div className=\"icon-display\">\n    <i className=\"bxdocs bxf-200-heart\" style={{ color: '#fb2d2d' }}></i>\n    <i className=\"bxdocs bx-twitter\" style={{ color: '#1da1f2' }}></i>\n</div>\n\nYou can also control the color using CSS classes:\n```css\ni.bx {\n  color: #fb2d2d;\n}\n```\n\n<br/>\n## Duotone Icons\nDuotone icons are made up of two layers: a primary layer and a secondary layer. You can style each layer independently using CSS variables.\n##### Color variables\n* `--bx-duotone-primary-color`\n* `--bx-duotone-secondary-color`\n##### Opacity variables\n* `--bx-duotone-primary-opacity` default:`1`\n* `--bx-duotone-secondary-opacity` default:`0.4`\n\n```html copy\n<i\n  class=\"bxd bx-heart-break\"\n  style=\"\n    --bx-duotone-primary-color:#2cdc4b;\n    --bx-duotone-primary-opacity:0.9;\n    --bx-duotone-secondary-color:#9d25ff;\n    --bx-duotone-secondary-opacity:0.6;\n  \"\n></i>\n\n<i\n  class=\"bxds bx-heart-break\"\n  style=\"\n    --bx-duotone-primary-opacity:0.9;\n    --bx-duotone-secondary-opacity:0.6;\n  \"\n></i>\n```\n<div className=\"icon-display\">\n    <i className=\"bxdocs bxrd-heart-break\" style={{ '--bx-duotone-primary-color': '#2cdc4b', '--bx-duotone-secondary-color': '#9d25ff', '--bx-duotone-primary-opacity': 0.9, '--bx-duotone-secondary-opacity': 0.6 }}></i>\n    <i className=\"bxdocs bxrds-heart-break\" style={{ '--bx-duotone-primary-opacity': 0.9, '--bx-duotone-secondary-opacity': 0.6 }}></i>\n</div>\n\n\n##### Inverting Layer Opacity\nIf you simply want to swap the opacity values between the two layers, you can use the `bx-invert-opacity` utility class. This inverts the layer opacities without requiring you to manually set any values.\n\n```html copy\n<i class=\"bxd bx-heart-break bx-invert-opacity\"></i>\n<i class=\"bxds bx-heart-break bx-invert-opacity\"></i>\n```\n<div className=\"icon-display\">\n    <i className=\"bxdocs bxrd-heart-break \" style={{ '--bx-duotone-secondary-opacity': 1,'--bx-duotone-primary-opacity': 0.4}}></i>\n    <i className=\"bxdocs bxrds-heart-break \" style={{ '--bx-duotone-secondary-opacity': 1,'--bx-duotone-primary-opacity': 0.4}} ></i>\n</div>";
