export const sourceCode = "# Remove Padding\nBoxicons by default come with some padding around the icons to ensure they are visually balanced. However, in certain design scenarios, you might want to remove this padding for a tighter fit.\n\n## How to Remove Padding\nTo remove the default padding from Boxicons, you can add the following CSS class to your icon element:\n`bx-remove-padding`\nThis class eliminates the extra space around the icon, allowing it to fit more snugly within its container.\nNo need to import any additional CSS files; the class is included in the main Boxicons stylesheet.\n```html copy\n<i class=\"bxf bx-golf-ball\">\n<i class=\"bxf bx-golf-ball bx-remove-padding\">\n```\n<div className=\"icon-display\">\n<i className=\"bxdocs bxrf-200-golf-ball\" style={{backgroundColor: '#c5ff0a', color: '#000'}}></i>\n<i className=\"bxdocs bxrf-200-golf-ball bx-remove-padding\" style={{backgroundColor: '#c5ff0a', color: '#000'}}></i>\n</div>";
